'use client';

import { useState, useEffect } from 'react';
import PrivateRoute from "@/app/components/PrivateRoute";
import Footer from "@/app/components/Footer";
import { useAuth } from '@/contexts/AuthContext';
import { collection, query, where, getDocs } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { useRouter } from 'next/navigation';

export default function MisMascotasPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [mascotas, setMascotas] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!user) return;

    const fetchMascotas = async () => {
      try {
        const q = query(collection(db, 'mascotas'), where('dueño_uid', '==', user.uid));
        const querySnapshot = await getDocs(q);
        const listaMascotas = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setMascotas(listaMascotas);
      } catch (error) {
        console.error("Error al cargar las mascotas: ", error);
      } finally {
        setLoading(false);
      }
    };

    fetchMascotas();
  }, [user]);

  return (
    <PrivateRoute>
      <main className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold">Mis Mascotas</h1>
          <button 
            onClick={() => router.push('/mascotas/nueva')}
            className="bg-violet-500 hover:bg-violet-700 text-white font-bold py-2 px-4 rounded-lg shadow-md"
          >
            + Agregar Mascota
          </button>
        </div>

        {loading ? (
          <p>Cargando mascotas...</p>
        ) : mascotas.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {mascotas.map(mascota => (
              <div key={mascota.id} className="bg-white p-6 rounded-lg shadow-lg">
                <h2 className="text-2xl font-bold mb-2">{mascota.nombre}</h2>
                <p><strong>Especie:</strong> {mascota.especie}</p>
                <p><strong>Edad:</strong> {mascota.edad} años</p>
                <p><strong>Sexo:</strong> {mascota.sexo}</p>
              </div>
            ))}
          </div>
        ) : (
          <p className="text-center text-gray-500">Aún no tienes mascotas registradas. ¡Agrega una!</p>
        )}

      </main>
      <Footer />
    </PrivateRoute>
  );
}
